/* Modern Table Styles */
table {
  width: 100%;
  border-collapse: collapse;
  background: transparent;
}

thead {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.08));
  backdrop-filter: blur(20px);
}

th {
  padding: 20px 16px;
  text-align: left;
  font-weight: 600;
  color: var(--light-text);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

td {
  padding: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  color: var(--light-text);
}

tbody tr {
  transition: all 0.3s ease;
}

tbody tr:hover {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.05));
}

/* Modern Form Elements */
select {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.12) 0%, rgba(255, 255, 255, 0.06) 100%);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 16px 20px;
  color: var(--light-text);
  backdrop-filter: blur(20px);
  transition: all 0.3s ease;
}

textarea {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.12) 0%, rgba(255, 255, 255, 0.06) 100%);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 16px 20px;
  color: var(--light-text);
  backdrop-filter: blur(20px);
  transition: all 0.3s ease;
  resize: vertical;
  font-family: var(--font-body);
}

/* Modern Status Badges */
.status-badge {
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-active {
  background: var(--success-gradient);
  color: white;
}

.status-inactive {
  background: rgba(239, 68, 68, 0.2);
  color: #fecaca;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.status-pending {
  background: var(--warning-gradient);
  color: white;
}

/* Modern Checkbox and Radio */
input[type="checkbox"] {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 6px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  background: transparent;
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;
}

input[type="checkbox"]:checked {
  background: var(--primary-gradient);
  border-color: transparent;
}

input[type="checkbox"]:checked::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 12px;
  font-weight: bold;
}

/* Responsive Design */
@media (max-width: 768px) {
  .listings-grid {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 16px;
  }
  
  .cataloro-button-primary,
  .cataloro-button-secondary {
    padding: 12px 20px;
    font-size: 13px;
  }
  
  .modal-content {
    padding: 24px;
    margin: 20px;
  }
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--primary-gradient);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--secondary-gradient);
}

/* ===== ENHANCED NOTIFICATION ANIMATIONS ===== */
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes animate-in {
  from {
    opacity: 0;
    transform: translateX(10px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate-in {
  animation: animate-in 0.3s ease-out;
}

/* Custom Scrollbar for Notifications */
.custom-scrollbar::-webkit-scrollbar {
  width: 6px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 10px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}

/* Line clamp utility */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Notification Toast Animations */
@keyframes toast-slide-in {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes toast-slide-out {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(100%);
    opacity: 0;
  }
}

.toast-slide-in {
  animation: toast-slide-in 0.3s ease-out;
}

.toast-slide-out {
  animation: toast-slide-out 0.3s ease-in;
}

/* ===== NOTIFICATION ICON BUTTON TOOLTIPS ===== */
/* Enhanced tooltip animations */
@keyframes tooltip-fade-in {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(2px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.group\/tooltip:hover .tooltip-content {
  animation: tooltip-fade-in 0.2s ease-out;
}

/* Icon button hover effects */
.notification-icon-btn {
  transition: all 0.2s ease-in-out;
  position: relative;
}

.notification-icon-btn:hover {
  transform: scale(1.1);
  filter: brightness(1.1);
}

.notification-icon-btn:active {
  transform: scale(0.95);
}

/* Tooltip arrow styling */
.tooltip-arrow {
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid #1f2937;
}

.dark .tooltip-arrow {
  border-top-color: #374151;
}

/* Better tooltip positioning */
.tooltip-wrapper {
  position: relative;
  display: inline-block;
}

.tooltip-content {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 8px;
  padding: 6px 8px;
  background-color: #1f2937;
  color: white;
  font-size: 11px;
  border-radius: 4px;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease-in-out;
  z-index: 1000;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.dark .tooltip-content {
  background-color: #374151;
}

.tooltip-wrapper:hover .tooltip-content {
  opacity: 1;
}

/* ===== NOTIFICATION REMOVAL ANIMATIONS ===== */
/* Smooth notification removal animation */
@keyframes notification-slide-out-right {
  0% {
    opacity: 1;
    transform: translateX(0);
    max-height: 200px;
    padding: 1rem 1.5rem;
    margin-bottom: 0;
  }
  50% {
    opacity: 0.5;
    transform: translateX(50px);
    max-height: 200px;
    padding: 1rem 1.5rem;
    margin-bottom: 0;
  }
  100% {
    opacity: 0;
    transform: translateX(100px);
    max-height: 0;
    padding: 0 1.5rem;
    margin-bottom: 0;
    overflow: hidden;
  }
}

.notification-removing {
  animation: notification-slide-out-right 0.3s ease-in forwards;
  pointer-events: none;
}

/* Notification action success feedback */
@keyframes action-success-flash {
  0% { background-color: transparent; }
  50% { background-color: rgba(34, 197, 94, 0.2); }
  100% { background-color: transparent; }
}

.notification-action-success {
  animation: action-success-flash 0.5s ease-out;
}

/* Enhanced notification dropdown animations */
.notifications-dropdown-enhanced {
  animation: slideDown 0.3s ease-out;
}

/* Notification item hover improvements */
.notification-item {
